// Constants
$max-container-size: 1920;
$startSize: 20;
$endSize: 12;
:root {
    --bg: #e5ebee;
    --light: rgba(255, 255, 255, .5);
    --dark: rgba(0, 0, 0, 0.5);
    --text: #5c5e64;
    --text2: ;
    --defArr: #363636;
    --secArr: #c93939;
    --minArr: #1eb14a;
    --hourArr: #201eb1;
    --textShadow: 0.033em 0.033em 0.1em rgba(0, 0, 0, 0.5);
    --arrShadow: 
        0.033em 0.033em 0.1em rgba(0, 0, 0, 0.5),
        inset 0.033em 0.033em 0.033em rgba(255, 255, 255, .2),
        inset -0.033em -0.033em 0.033em rgba(0, 0, 0, 0.5);
    --btnShadow:
        0.067em 0.067em 0.1em rgba(0, 0, 0, 0.4),
        inset 0.067em 0.067em 0.1em rgba(255, 255, 255, .5),
        inset -0.067em -0.067em 0.067em rgba(0, 0, 0, 0.5);
    --btnShadow_hover:
        0.055em 0.055em 0.08em rgba(0, 0, 0, 0.2),
        inset 0.067em 0.067em 0.1em rgba(255, 255, 255, .25),
        inset -0.067em -0.067em 0.067em rgba(0, 0, 0, 0.25);
    --btnShadow_active:
        -0.055em -0.055em 0.08em rgba(0, 0, 0, 0.2),
        inset -0.067em -0.067em 0.1em rgba(255, 255, 255, .25),
        inset 0.067em 0.067em 0.067em rgba(0, 0, 0, 0.25);
}
.theme-dark {
    --bg: #0e0c1d;
    --light: rgba(137, 168, 255, 0.5);
    --dark: rgba(0, 0, 0, 0.5);
    --text: #e3e3e4;
    --defArr: #cccbbf;
    --secArr: #f34141;
    --minArr: #34e468;
    --hourArr: #3936e4;
    --textShadow: 0 0 0.1em rgba(255, 255, 255, 0.5);
    --arrShadow: 
        0 0 0.4em rgba(255, 255, 255, 0.438);
    --btnShadow:
        0.067em 0.067em 0.1em rgba(0, 0, 0, 0.4),
        inset 0.067em 0.067em 0.1em rgba(137, 168, 255, 0.5),
        inset -0.067em -0.067em 0.067em rgba(0, 0, 0, 0.5);
    --btnShadow_hover:
        0.055em 0.055em 0.08em rgba(0, 0, 0, 0.2),
        inset 0.067em 0.067em 0.1em rgba(137, 168, 255, 0.25),
        inset -0.067em -0.067em 0.067em rgba(0, 0, 0, 0.25);
    --btnShadow_active:
        -0.055em -0.055em 0.08em rgba(0, 0, 0, 0.2),
        inset -0.067em -0.067em 0.1em rgba(137, 168, 255, 0.25),
        inset 0.067em 0.067em 0.067em rgba(0, 0, 0, 0.25);
}
//

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    @include adaptive-value(font-size, $startSize, $endSize);
    padding: 1em;
    &__theme-btn {
        cursor: pointer;
        color: var(--text);
        transition: 0.2s;
        background: transparent;
        height: 1.5em;
        width: 1.5em;
        border: none;
        border-radius: 50%;
        box-shadow: var(--btnShadow);
        &:hover {
            // cursor: pointer;
            box-shadow: var(--btnShadow_hover);
        }
        &:active {
            transition: 0.05s;
            // cursor: pointer;
            box-shadow: var(--btnShadow_active);
        }
    }
}

body {
    background: var(--bg);
    transition: 0.4s;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    height: 100vh;
    @media (max-width: 1000px) {
        flex-direction: column;
    }
}

.clock-container {
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    @include adaptive-value(font-size, $startSize, $endSize);
    color: var(--text);
    @media (max-width: 1000px) {
        margin-bottom: 4em;
    }
    @media (min-width: 1000px) {
        margin-right: 4em;
    }
    // border-right: solid 0.1em #5c5e64;
}

.title {
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 1em;
    text-shadow: var(--textShadow);
    @media (max-width: 1000px) {
        @include adaptive-value(font-size, 30, 20);
    }
}

.clock {
    font-size: 30px;
    @include adaptive-value(font-size, $startSize, $endSize);
    @media (max-width: 1000px) {
        @include adaptive-value(font-size, 30, 20);
    }
    position: relative;
    width: 13.333em;
    height: 13.333em;
    background: transparent;
    border-radius: 50%;
    box-shadow: 
        0.067em 0.067em 0.167em var(--dark),
        inset 0.067em 0.067em 0.1em var(--light),
        inset -0.067em -0.067em 0.067em var(--dark);
    &__center {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        height: 0.5em;
        width: 0.5em;
        border-radius: 50%;
        background: var(--defArr);
        box-shadow: var(--arrShadow);
    }
    &__dot-container {
        display: flex;
        justify-content: flex-end;
        width: 6em;
        position: absolute;
        transform-origin: 0 50%;
        top: 50%;
        left: 50%;
        @for $i from 1 through 12 {
            &:nth-child(#{$i}) {
                transform: translate(0, -50%) rotate((360 / 12 * $i) + deg);
            }
        }
    }
    &__dot {
        background: var(--defArr);
        opacity: 0.7;
        border-radius: 0.05em;
        width: 0.267em;
        height: 0.1em;
    }
    &__second-arrow {
        position: absolute;
        transform-origin: 0 50%;
        transform: translate(0, -50%);
        top: 50%;
        left: 50%;
        height: 0.133em;
        width: 6em;
        border-radius: 0.067em;
        background: var(--secArr);
        box-shadow: var(--arrShadow);
    }
    &__long-arrow {
        position: absolute;
        transform-origin: 0 50%;
        transform: translate(0, -50%);
        top: 50%;
        left: 50%;
        height: 0.2em;
        width: 5.333em;
        border-radius: 0.1em;
        background: var(--defArr);
        box-shadow: var(--arrShadow);
    }
    &__short-arrow {
        position: absolute;
        transform-origin: 0 50%;
        transform: translate(0, -50%);
        top: 50%;
        left: 50%;
        height: 0.2em;
        width: 3.333em;
        border-radius: 0.1em;
        background: var(--defArr);
        box-shadow: var(--arrShadow);
    }
}

.timer-container {
    margin: 0;
}

.timer {
    @include adaptive-value(font-size, $startSize, $endSize);
    display: flex;
    margin-bottom: 1em;
    &__hours, &__minutes, &__seconds {
        @include adaptive-value(font-size, 18, 10);
        @media (max-width: 1000px) {
            @include adaptive-value(font-size, 15, 7);
        }
        &:before {
            content: "";
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 10em;
            font-family: 'Montserrat', sans-serif;
            color: var(--text);
            opacity: 0.1;
        }
    }
    &__hours {
        &:before {
            content: "H";
        }
        margin-right: 1em;
    }
    &__minutes {
        &:before {
            content: "M";
        }
        margin-right: 1em;
    }
    &__seconds {
        &:before {
            content: "S";
        }
    }
    &__hours-arrow, &__minutes-arrow, &__seconds-arrow {
        transition: 1s;
        position: absolute;
        transform-origin: 0 50%;
        transform: translate(0, -50%) rotate(-90deg);
        top: 50%;
        left: 50%;
        height: 0.2em;
        width: 6em;
        border-radius: 0.1em;
        box-shadow: var(--arrShadow);
    }
    &__hours-arrow {
        background: var(--hourArr);
    }
    &__minutes-arrow {
        background: var(--minArr);
    }
    &__seconds-arrow {
        background: var(--secArr);
    }
}

.text-input {
    background: transparent;
    height: 1.5em;
    width: 4em;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    color: var(--text);
    border: none;
    border-radius: 0.75em;
    box-shadow: 
    -0.067em -0.067em 0.1em var(--dark),
    0.067em 0.067em 0.1em var(--light),
    inset -0.067em -0.067em 0.1em var(--light),
    inset 0.067em 0.067em 0.067em var(--dark);
    &::placeholder {
        color: var(--text);
        opacity: 0.6;
    }
    &:focus {
        outline: none;
        &::placeholder {
            transition: 0.1s;
            opacity: 0;
        }
    }
}

.input {
    &__start {
        &:before {
            content: "Start";
        }
        &.stop:before {
            content: "Stop";
        }
        color: var(--text);
        font-family: 'Montserrat', sans-serif;
        transition: 0.2s;
        margin-left: 1em;
        background: transparent;
        height: 1.5em;
        padding: 0 1.5em;
        border: none;
        border-radius: 0.75em;
        box-shadow: var(--btnShadow);
        &:hover {
            cursor: pointer;
            box-shadow: var(--btnShadow_hover);
        }
        &:active {
            transition: 0.05s;
            cursor: pointer;
            box-shadow: var(--btnShadow_active);
        }
    }
}